* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

#main-canvas {
  border: 1px solid #000;
}

#toolbox {
  width: 30vw;
  height: 90vh;
  position: absolute;
  top: 1em;
  right: 1em;
}

#prim-option-box {
  grid-template-columns: repeat(auto-fit, 4em);
  grid-auto-rows: 4em;
  gap: .5em;
  height: 25vh;
  display: grid;
  overflow-y: auto;
}

#prim-used-box {
  grid-template-columns: 1fr;
  grid-auto-rows: 4em;
  gap: .5em;
  height: 25vh;
  display: grid;
  overflow-y: auto;
}

.image-button {
  background-color: #0000;
  border: 1px solid #000;
}

.image-button:hover {
  outline: 1px solid #000;
}

.image-icon {
  max-width: 100%;
  max-height: 100%;
}

.prim-entry {
  border: 1px solid #000;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  padding: 0 1em;
  display: grid;
}

.prim-delete {
  justify-self: end;
}

.hidden {
  visibility: hidden;
}

#controls-set {
  flex-direction: column;
  display: flex;
}
